import React from "react";

import Cell from "./Cell";
import { customers } from "./messages";
import { Title, Wrapper, Grid, Pair } from "./elements";

const CustomerGrid = () => (
  <Wrapper>
    <Title>
      Pharmaceutical companies trust Brex to save time and optimize their spend.
    </Title>
    <Grid>
      {customers.map((pair, pairIndex) => (
        <Pair key={`pairs-${pairIndex}`}>
          {pair.map((cell, cellIndex) => (
            <Cell
              cell={cell}
              index={pairIndex * 2 + cellIndex}
              key={`cell-${pairIndex}-${cellIndex}`}
            />
          ))}
        </Pair>
      ))}
    </Grid>
  </Wrapper>
);

export default CustomerGrid;
