import React, { useState } from "react";

import { Cell, Logo, TextWrapper, Description } from "./elements";

interface CustomerCellProps {
  cell: {
    height: number;
    width: number;
    logo: string;
    description: string;
  };
  index: number;
}

const CustomerCell = ({ cell, index }: CustomerCellProps) => {
  const [hovering, setHovering] = useState(false);

  return (
    <Cell
      key={`cell-${index}`}
      onMouseOver={() => setHovering(true)}
      onFocus={() => setHovering(true)}
      onMouseOut={() => setHovering(false)}
      onBlur={() => setHovering(false)}
      index={index}
    >
      <Logo
        height={cell.height}
        width={cell.width}
        src={cell.logo}
        hovering={hovering}
      />
      <TextWrapper hovering={hovering}>
        {/* Removed Name to make all descriptions fit comfortably */}
        {/* <Name>{cell.name}</Name> */}
        <Description>{cell.description}</Description>
      </TextWrapper>
    </Cell>
  );
};

export default CustomerCell;
