import styled from "@emotion/styled";
import colors from "styles/colors";
import { breakPoints } from "components/Grid";
import { gothamFonts } from "styles/fonts";

interface CellProps {
  index: number;
}

const lightGrayBorder = `solid 1px ${colors.lightGray}`;

interface TextWrapperProps {
  hovering: boolean;
}

interface LogoProps {
  height: number;
  width: number;
  hovering: boolean;
}

const lineForAllElementsExceptLastMediumScreen = (index: number) =>
  index / 2 < 7;
const lineForAllElementsExceptLastSmallScreen = (index: number) => index === 15;
const lineForAllElementsInRightColumnMediumScreen = (index: number) =>
  index % 2 === 1;

const lineForAllRowsExceptLast = (index: number) => index / 4 < 3;

const lineForAllColumnsExceptFirst = (index: number) => index % 4 !== 0;

export const Cell = styled("div")<CellProps>(({ index }) => [
  {
    width: 245,
    height: 181,
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    color: colors.black,
    [breakPoints.medium]: {
      borderBottom: lineForAllElementsExceptLastMediumScreen(index)
        ? lightGrayBorder
        : "none",
      borderLeft: lineForAllElementsInRightColumnMediumScreen(index)
        ? lightGrayBorder
        : "none",
      width: "50%",
    },
    [breakPoints.small]: {
      borderBottom: lineForAllElementsExceptLastSmallScreen(index)
        ? "none"
        : lightGrayBorder,
      borderLeft: "none",
      width: "100%",
      minWidth: 320,
    },
  },
  lineForAllRowsExceptLast(index) && {
    borderBottom: lightGrayBorder,
  },
  lineForAllColumnsExceptFirst(index) && {
    borderLeft: lightGrayBorder,
  },
]);

export const TextWrapper = styled("div")<TextWrapperProps>(({ hovering }) => [
  {
    height: "100%",
    width: "100%",
    padding: 24,
    backgroundColor: colors.primary,
    display: "none",
    border: "none",
  },
  hovering && {
    display: "flex",
    flexDirection: "column",
  },
]);

export const Name = styled("div")({
  ...gothamFonts["body-xl-medium"],
  color: colors.white,
});

export const Description = styled("div")({
  fontSize: 13,
  lineHeight: "20px",
  color: colors.white,
});

export const Logo = styled("img")<LogoProps>(({ height, width, hovering }) => [
  {
    height,
    width,
    display: "flex",
  },
  hovering && { display: "none" },
]);
