import React from "react";

import { Row, Column } from "components/Grid";
import {
  Section,
  Title,
  Subtitle,
  Container,
  BigMultiplier,
  BigMultiplierTitle,
  BigMultiplierSubtitle,
  SmallMultiplierWrapper,
  AmountContainer,
  SmallMultiplierAmount,
  SmallMultiplierCaption,
  Bar,
  ColumnWrapper,
} from "./elements";

interface SmallMultiplierProps {
  amount: number;
  caption: string;
  bottomPadding: boolean;
}

const SmallMultiplier = ({
  amount,
  caption,
  bottomPadding,
}: SmallMultiplierProps) => (
  <SmallMultiplierWrapper bottomPadding={bottomPadding}>
    <Bar />
    <AmountContainer>
      <SmallMultiplierAmount>{`${amount}x`}</SmallMultiplierAmount>
      <SmallMultiplierCaption>{caption}</SmallMultiplierCaption>
    </AmountContainer>
  </SmallMultiplierWrapper>
);

const smallMultipliers = [
  { amount: 7, caption: "on conference tickets" },
  { amount: 4, caption: "on brex travel" },
  { amount: 1, caption: "on everything else" },
];

const Multipliers = () => (
  <Section>
    <Title>Built to earn you more</Title>
    <Subtitle>
      Make Brex your exclusive corporate card to optimize money back to your
      business, with categories built for life sciences.
    </Subtitle>
    <Container>
      <Row padding={30} gutterWidth={26}>
        <Column span={1} />
        <Column span={5} spanMedium={10}>
          <ColumnWrapper>
            <BigMultiplier>2x</BigMultiplier>
            <BigMultiplierTitle>ON LAB SUPPLIES</BigMultiplierTitle>
            <BigMultiplierSubtitle>
              Get points back on all of your lab supply spend on your most
              important vendors.
            </BigMultiplierSubtitle>
          </ColumnWrapper>
        </Column>
        <Column span={1} />
        <Column span={0} spanMedium={1} />
        <Column span={4} spanMedium={10}>
          <ColumnWrapper>
            {smallMultipliers.map((multiplier, index) => (
              <SmallMultiplier
                amount={multiplier.amount}
                caption={multiplier.caption}
                key={`${multiplier.caption}-key`}
                bottomPadding={index !== smallMultipliers.length - 1}
              />
            ))}
          </ColumnWrapper>
        </Column>
        <Column span={1} />
      </Row>
    </Container>
  </Section>
);

export default Multipliers;
